<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-lowongan-pelamar',
  data () {
    return {
      config: {
        title: 'Pelamar Lowongan',
        model_api: 'lowongan-pelamar',
        getter: 'lowongan-pelamar',
        setter: 'lowongan-pelamar',
        pk_field: null,
        permission: {
          create: 'create-lowongan-pelamar',
          read: 'view-lowongan-pelamar',
          update: 'update-lowongan-pelamar',
          delete: 'delete-lowongan-pelamar'
        },
        slave: [],
        fields: [
          { id: 'lowongan_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'address', label: 'Alamat', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'fullname', label: 'Nama Tenaga Kerja' },
          { id: 'nik', label: 'NIK' },
          { id: 'place_of_birth', label: 'Tempat Lahir' },
          { id: 'telephone', label: 'No. Telp' },
          { id: 'email', label: 'Email' },
          { id: 'program_study', label: 'Program Studi' },
          { id: 'institution_name', label: 'Nama Institusi' },
          { id: 'date_of_birth', label: 'Tanggal Lahir', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date', validation: ['required'] }, update: { type: 'date', validation: ['required'] }, filter: false } },
          {
            id: 'gender',
            label: 'Jenis Kelamin',
            methods: {
              list: { transform: 'kelamin' },
              detail: { transform: 'kelamin' },
              create: {
                type: 'radio',
                value: 'male',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Laki - Laki', code: 'male' }, { label: 'Perempuan', code: 'female' }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Laki - Laki', code: 'male' }, { label: 'Perempuan', code: 'female' }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Laki - Laki', code: 'male' }, { label: 'Perempuan', code: 'female' }]
                  }
                }
              }
            }
          },
          {
            id: 'marital_status',
            label: 'Status Kawin',
            methods: {
              list: { transform: 'kawin' },
              detail: { transform: 'kawin' },
              create: {
                type: 'radio',
                value: 'single',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Belum Menikah', code: 'single' }, { label: 'Menikah', code: 'married' }, { label: 'Janda/Duda', code: 'widow' }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Belum Menikah', code: 'single' }, { label: 'Menikah', code: 'married' }, { label: 'Janda/Duda', code: 'widow' }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Belum Menikah', code: 'single' }, { label: 'Menikah', code: 'married' }, { label: 'Janda/Duda', code: 'widow' }]
                  }
                }
              }
            }
          },
          {
            id: 'religion_id',
            label: 'Agama',
            methods: {
              create: {
                setter: 'master-religions',
                getter: 'master-religions',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-religions',
                getter: 'master-religions',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-religions',
                getter: 'master-religions',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'img_photo',
            label: 'Foto Pelamar',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'img_photo.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*' }, { limit: 5 }],
                view_data: 'img_photo'
              }
            }
          },
          {
            id: 'education_id',
            label: 'Pendidikan Terakhir',
            methods: {
              create: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'nationality',
            label: 'Kewarganagaraan',
            methods: {
              list: { transform: 'uppercase' },
              detail: { transform: 'uppercase' },
              create: {
                type: 'radio',
                value: 'wni',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'WNI', code: 'wni' }, { label: 'WNA', code: 'wna' }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'WNI', code: 'wni' }, { label: 'WNA', code: 'wna' }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'WNI', code: 'wni' }, { label: 'WNA', code: 'wna' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



